*{
    background-color: #000511;
    color: white;
    font-family: sans-serif;
}
.header_container{
    display: flex;
    flex-direction: row;
    position: relative;
    padding: 10px 5%;
    
}

.logo_section img{
    background-image: url('./assets/logo.svg');
    background-repeat: no-repeat;
    width: 122px;
    height: 65px;
    background-position: center;
    /* transform: scale(1.5); */
    /* padding-top: 4px; */
    margin-left: 10px;
    vertical-align: middle;
    /* padding: 24px 6px; */
}

.background{
    z-index: -2;
    width: 100%;
    height: auto;
    background-image: radial-gradient(circle at 50% -30%, #232e4e 23%, rgba(105, 120, 182, 0) 55%);
    position: absolute;
    top: 0%;
    bottom: 0%;
    left: 0%;
    right: 0%;
    overflow: hidden;
}

.logo_section{
    width: 35%;
    
}

.tabs_sections{
    width: 55%;
    display: flex;
    position: relative;
    justify-content: center;
    gap: 30px;
    margin: auto;
    cursor: pointer;
    /* color: white */
}

/* Section 1 CSS  */

.section_1, .section_2{
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 50px 5%;
}

.btn_div{
    display: flex;
    justify-content: center;
}
.sec1_btn{
    
    border-radius: 30px;
    /* outline: none; */
    font-size: 14px;
    border: 1px solid rgba(255, 255, 255, .55);;
    height: 30px;
    align-items: center;
    padding: 5px 20px 5px 24px;
    text-decoration: none;
    display: flex;
    box-shadow: 10px 0 40px rgba(255, 255, 255, .1);
    
}

.sec1_btn img, #news_img, #twitter_img, #linkedin_img{
    background-image: url('./assets/arrow.svg');
    background-repeat: no-repeat;
    width: 14px;
    height: 6px;
    background-position: center;
    transform: scale(1.5);
    padding-top: 4px;
    margin-left: 10px;
    vertical-align:middle;
}

.sec1_heading, .sec2_heading{
    /* max-width: 13ch; */
    font-size: 70px;
    margin: auto;
    margin-top: 10px;
    text-align: center;
}

.sec_content{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: 10px 0;
}

.content1, .content2{
    display: flex;
    flex-direction: column;
    /* border-radius: 8px; */
    width: 31%;
    border: 1px solid rgba(255, 255, 255, .55);
    box-shadow: 10px 0 40px rgba(255, 255, 255, .1);
}

.content1 img, .content2 img{
    background-image: url('./assets/img1.jpg');
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    height: 200px;
}

.content_text{
    font-size: 20px;
    line-height: 30px;
    /* margin: 10px 0; */
    padding: 10px 20px;
    text-align: justify;
    /* width: 50%; */
    /* padding: 40px 60px; */
}

.sec2_heading{
    font-size: 50px;
}

.sec2_content{
    margin: 0 100px;
    box-shadow: 10px 0 40px rgba(255, 255, 255, .1);
    border-radius: 4px;
    margin-top: 20px;
    padding: 15px;
}

.para{
    font-size: 20px;
    line-height: 35px;
    text-align: center;
}

#p2{
    margin: 0 50px;
}

.section_3{
    width: 100%;
    position: relative;
    display: flex;
    margin: 60px 0;
    
    /* overflow: hidden; */
}

.full-width-video {
    width: 70%;
    height: 500px;
    margin: auto;
    box-shadow: 10px 0 40px rgba(255, 255, 255, .1);
    /* height: auto;
    position: absolute;
    top: 0;
    left: 0; */
}

.footer{
    border-top: 1px solid rgba(255, 255, 255, .1);
}

.footer_div{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 20px 80px;
    gap: 20px;
}

.footer_col img{
    background-image: url('./assets/logo.svg');
    background-repeat: no-repeat;
    width: 122px;
    height: 65px;
    background-position: center;
    vertical-align: middle;
}

.newsletter{
    border-bottom: 1px solid white;
    margin-top: 16px;
}

.newsletter span{
    margin-right: 10px;
    
}

.footer_col input{
    border: none;
    /* border-bottom: 1px solid white; */
    outline: none;
}

#twitter_img{
    background: url('./assets/twitter.png');
}

#linkedin_img{
    background: url('./assets/linkedin.png');
}

.social_icon{
    margin: 20px 0; 
}

.social_icon a{
    margin-right: 10px;
}

.copyright{
    margin: 0 70px;
}


/* Media Queries  */

@media (max-width: 991px){
    .sec1_heading{
        font-size: 55px;
    }

    .content1, .content2{
        width: 41%;
    }

    .footer_col{
        margin-right: 30px;
    }
}

@media (max-width: 600px){
    .footer_div{
        margin: 20px 30px;
        gap: 0px;
    }

    .footer_col{
        margin-right: 20px;
    }

    .sec_content{
        flex-direction: column;
        gap: 30px;
    }

    .content1, .content2{
        width: 70%;
        margin: auto;
    }
    
    .sec1_heading, .sec2_heading{
        font-size: 35px;
        line-height: 50px;
    }

    .sec2_content{
        margin: 0 60px;
    }

    #p2{
        margin: 0 30px;
    }
}

@media (max-width: 500px){
    .footer_div{
        flex-wrap: wrap;
    }

    .content1, .content2{
        width: 95%;
    }

    .content_text{
        font-size: 16px;
        line-height: 25px;
    }

    .para{
        font-size: 16px;
        line-height: 30px;
    }

    .sec1_heading, .sec2_heading{
        font-size: 25px;
        line-height: 35px;
    }

    .sec2_content{
        margin: 0 10px;
    }

    .header_container{
        flex-direction: column;
    }

    .tabs_sections{
        margin-top: 20px;
        width: 90%;
        justify-content: space-between;
        gap: 10px;
    }

    .tabs_sections div{
        font-size: 14px;
    }

    .logo_section{
        width: 90%;
        text-align: center;
    }
}